import React from 'react';
import api from '../../../Global/Api';
import { Form,Input,Radio,Button,Modal,InputNumber,Select,Checkbox} from 'antd';
import {Row, Col, Card} from 'react-bootstrap';
import Aux from "../../../../hoc/_Aux";

const { TextArea } = Input;

function onChange(value) {
  console.log(`selected ${value}`);
}

function onBlur() {
  console.log('blur');
}

function onFocus() {
  console.log('focus');
}

function onSearch(val) {
  console.log('search:', val);
}

class RegistrationForm extends React.Component {
  constructor(props){
    super(props);
    this.initialState = {
      hotel:{},
      propertytype:[],
      hotelcategory:[],
      hotelStarcategory:[],
      hotelfacility:[],
      hotelroomamenities:[],
      hotelowner:[],
      citydata:[],
      HotelId : this.props.match.params.HotelId,
    };
    

    if(this.hotel){
      this.state = this.hotel
    } else {
      this.state = this.initialState;
    }

    this.handleSubmit = this.handleSubmit.bind(this);
  }
  
  success() {
    Modal.success({
      content: 'Successively Updated',
    });
  }
  
  error() {
    Modal.error({
      title: 'This is an error message',
      content: 'Error Occurred!!',
    });
  }

  componentDidMount() {
    this.fetch();
    this.fetchpropertytype();
    this.fetchhotelcategory();
    this.fetchhotelStarcategory();
    this.fetchhotelFacility();
    this.fetchhotelroomamenities();
    this.fetchhotelowner();
    this.fetchcity();
  }

  fetch = () => {
    this.setState({ loading: true });
    api.get(`/api/hotel/${this.state.HotelId}`,{}).then(res => {
        this.setState({
          loading: false,
          hotel: res.data,
        });
      });
  };

  fetchpropertytype = (params = {}) => {
    api.get(`/api/hotelpropertytype`,{}).then(res => {
        this.setState({
          propertytype: res.data,
        });
      });
    };

    
  fetchhotelcategory = (params = {}) => {
    api.get(`/api/hotelcategory`,{}).then(res => {
        this.setState({
          hotelcategory: res.data,
        });
      });
    };

       
  fetchhotelStarcategory = (params = {}) => {
    api.get(`/api/hotelStarcategory`,{}).then(res => {
        this.setState({
          hotelStarcategory: res.data,
        });
      });
    };

    fetchhotelFacility = (params = {}) => {
      api.get(`/api/hotelfacility`,{}).then(res => {
          this.setState({
            hotelfacility: res.data,
          });
        });
      };

      
    fetchhotelroomamenities = (params = {}) => {
      api.get(`/api/hotelroomamenities`,{}).then(res => {
          this.setState({
            hotelroomamenities: res.data,
          });
        });
      };

      fetchhotelowner = (params = {}) => {
        api.get(`/api/hotelowner`,{}).then(res => {
            this.setState({
              hotelowner: res.data,
            });
          });
        };

        fetchcity = (params = {}) => {
          api.get(`/api/city`,{}).then(res => {
              this.setState({
                citydata: res.data,
              });
            });
          };
  
          handleSubmit = (e) => {
            e.preventDefault();
            this.props.form.validateFieldsAndScroll((err, value) => {
              if (!err) {
                console.log('Received values of form: ', value);
                this.setState({loading:true});
                if(this.props.match.params.HotelId){
                api.put(`/api/hotel/${this.state.HotelId}`, value, {
                  headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
                  .then(response => {
                    if(response.status === 200){
                        this.success();
                        this.fetch();
                    }
                  })
                  .catch(error => {
                    this.error();
                  });
                }
                  else{
                    api.post(`/api/hotel`, value, {
                  headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
                  .then(response => {
                    if(response.status === 201){
                        this.success();
                        this.fetch();
                    }
                  })
                  .catch(error => {
                    this.error();
                  });
                  }
                this.props.handleOk();
                this.setState(this.initialState);
              }
            });
          };

    render() {
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      const propertytypevalue = this.state.propertytype.map((prop_type) =>
      <option key={prop_type.id} value={prop_type.id}>{prop_type.hotel_property_type_name}</option>
    );

      const hotelcategoryvalue = this.state.hotelcategory.map((hotel_category) =>
      <option key={hotel_category.id} value={hotel_category.id}>{hotel_category.hotel_category_name}</option>
    );
  
    const hotelstarcategoryvalue = this.state.hotelStarcategory.map((hotel_star_category) =>
    <option key={hotel_star_category.id} value={hotel_star_category.id}>{hotel_star_category.hotel_star_category_name}</option>
    );

    const hotelfacilityvalue = this.state.hotelfacility.map((hotel_facility) =>
    <option key={hotel_facility.id} value={hotel_facility.id}>{hotel_facility.facility_name}</option>
    );

    const hotelroomamenitiesvalue = this.state.hotelroomamenities.map((hotel_room_amenities) =>
    <option key={hotel_room_amenities.id} value={hotel_room_amenities.id}>{hotel_room_amenities.amenity_name}</option>
    );

    const hotelownervalue = this.state.hotelowner.map((hotel_owner) =>
    <option key={hotel_owner.id} value={hotel_owner.id}>{hotel_owner.owner_name}</option>
    );

    const citydatavalue = this.state.citydata.map((city) =>
    <option key={city.id} value={city.id}>{city.city_name}</option>
    );
     
      return (
        <Aux>
        <Row>
            <Col>
                <Card>
                    <Card.Header>
                        <Card.Title as="h5">Hotel Form</Card.Title>
                    </Card.Header>
                    <Card.Body>
                         <Row>
                            <Col md={12}>
                                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                                <Row>
                                <Col md={6}>
                                <Form.Item label="Hotel Name">
                                  {getFieldDecorator('hotel_name', { initialValue: this.state.hotel.hotel_name,
                                    rules: [
                                      {
                                        required: true,
                                      },
                                    ],
                                  })(<Input />)}
                                </Form.Item>
                                </Col>
                                <Col md={6}>
                                  <Form.Item label="Hotel Email">
                                    {getFieldDecorator('hotel_email', { initialValue: this.state.hotel.hotel_email,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<Input />)}
                                  </Form.Item>

                                  </Col>
                                <Col md={6}>
                                  <Form.Item label="Hotel Contact">
                                    {getFieldDecorator('hotel_contact', { initialValue: this.state.hotel.hotel_contact,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<InputNumber style={{width: '100%' }}/>)}
                                  </Form.Item>
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Property Type" hasFeedback>
                                  {getFieldDecorator('hotel_property_type', { initialValue: this.state.hotel.hotel_property_type,
                                    rules: [{ required: true, message: 'Please select Property Type!' }],
                                  })(
                                    <Select placeholder="Please select Property Type!">
                                    {propertytypevalue}
                                    </Select>,
                                  )}
                                </Form.Item>  

                                </Col>
                                <Col md={6}>

                                <Form.Item label="Hotel Category" hasFeedback>
                                  {getFieldDecorator('hotel_category', { initialValue: this.state.hotel.hotel_category,
                                    rules: [{ required: true, message: 'Please select Hotel Catagory!' }],
                                  })(
                                    <Select placeholder="Please select Hotel Catagory!">
                                    {hotelcategoryvalue}
                                    </Select>,
                                  )}
                                </Form.Item>

                                </Col>
                                <Col md={6}>
                                <Form.Item label="Hotel Star Category" hasFeedback>
                                  {getFieldDecorator('hotel_star_category', { initialValue: this.state.hotel.hotel_star_category,
                                    rules: [{ required: true, message: 'Please select Hotel Star Catagory!' }],
                                  })(
                                    <Select placeholder="Please select Hotel Star Catagory!">
                                    {hotelstarcategoryvalue}
                                    </Select>,
                                  )}
                                </Form.Item>
                                
                                </Col>
                                <Col md={6}>
                              <Form.Item label="Hotel Facility">
                                {getFieldDecorator('hotel_facility', { initialValue: this.state.hotel.hotel_facility,
                                  rules: [
                                    { required: true, message: 'Please select Hotel Facility!', type: 'array' },
                                  ],
                                })(
                                  <Select mode="multiple" placeholder="Please select Hotel Facility!">
                                    {hotelfacilityvalue}
                                  </Select>,
                                )}
                              </Form.Item>
                              </Col>
                               <Col md={6}>
                              <Form.Item label="Hotel Room Amenities">
                                {getFieldDecorator('hotel_room_amenities', { initialValue: this.state.hotel.hotel_room_amenities,
                                  rules: [
                                    { required: true, message: 'Please select Hotel Room Amenities!', type: 'array' },
                                  ],
                                })(
                                  <Select mode="multiple" placeholder="Please select Hotel Room Amenities!">
                                    {hotelroomamenitiesvalue}
                                  </Select>,
                                )}
                              </Form.Item>

                              </Col>
                              <Col md={6}>
                              <Form.Item label="Hotel Owner" hasFeedback>
                                  {getFieldDecorator('hotel_owner', { initialValue: this.state.hotel.hotel_category,
                                    rules: [{ required: true, message: 'Please select Hotel Owner!' }],
                                  })(
                                    <Select placeholder="Please select Hotel Owner!">
                                    {hotelownervalue}
                                    </Select>,
                                  )}
                                </Form.Item>

                                </Col>
                                <Col md={6}>
                                <Form.Item label="City" hasFeedback>
                                    {getFieldDecorator('city', { initialValue: this.state.hotel.city,
                                      rules: [{ required: true, message: 'Please select City!' }],
                                    })(
                                      <Select placeholder="Please select City!"  
                                      showSearch
                                      optionFilterProp="children"
                                      onChange={onChange}
                                      onFocus={onFocus}
                                      onBlur={onBlur}
                                      onSearch={onSearch}
                                      filterOption={(input, option) =>
                                        option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                      }>
                                      {citydatavalue}
                                      </Select>,
                                    )}
                                  </Form.Item>

                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Hotel Address">
                                    {getFieldDecorator('hotel_add', { initialValue: this.state.hotel.hotel_add,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<TextArea rows={4}/>)}
                                  </Form.Item>
                                  
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Hotel landmark">
                                    {getFieldDecorator('hotel_landmark', { initialValue: this.state.hotel.hotel_landmark,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<TextArea rows={4}/>)}
                                  </Form.Item>
                                    
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Hotel description">
                                    {getFieldDecorator('hotel_description', { initialValue: this.state.hotel.hotel_description,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<TextArea rows={4}/>)}
                                  </Form.Item>
                                  
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Hotel policy">
                                    {getFieldDecorator('hotel_policy', { initialValue: this.state.hotel.hotel_policy,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<TextArea rows={4}/>)}
                                  </Form.Item>
                                  
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Hotel Around">
                                    {getFieldDecorator('hotel_around', { initialValue: this.state.hotel.hotel_around,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<TextArea rows={4}/>)}
                                  </Form.Item>

                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Hotel Checkin">
                                  {getFieldDecorator('hotel_checkin', { initialValue: this.state.hotel.hotel_checkin,
                                    rules: [
                                      {
                                        required: true,
                                      },
                                    ],
                                  })(<Input />)}
                                </Form.Item>
                                 
                                </Col>
                                  <Col md={6}>
                                <Form.Item label="Hotel Checkout">
                                  {getFieldDecorator('hotel_checkout', { initialValue: this.state.hotel.hotel_checkout,
                                    rules: [
                                      {
                                        required: true,
                                      },
                                    ],
                                  })(<Input />)}
                                </Form.Item>
                                
                                </Col>
                                  <Col md={6}>
                                <Form.Item label="Hotel maxoccupancy">
                                    {getFieldDecorator('hotel_maxoccupancy', { initialValue: this.state.hotel.hotel_maxoccupancy,
                                      rules: [
                                        {
                                          required: true,
                                        },
                                      ],
                                    })(<InputNumber style={{width: '100%' }}/>)}
                                  </Form.Item>
                                  <Form.Item {...tailFormItemLayout}>
                                    {getFieldDecorator('hotel_pureveg', { initialValue: this.state.hotel.hotel_pureveg,
                                      valuePropName: 'checked',
                                    })(
                                      <Checkbox>
                                        Hotel Pure Veg
                                      </Checkbox>,
                                    )}
                                  </Form.Item>
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item {...tailFormItemLayout}>
                                    {getFieldDecorator('hotel_sellonline', { initialValue: this.state.hotel.hotel_sellonline,
                                      valuePropName: 'checked',
                                    })(
                                      <Checkbox>
                                        Hotel Sellonline
                                      </Checkbox>,
                                    )}
                                  </Form.Item>
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item {...tailFormItemLayout}>
                                    {getFieldDecorator('hotel_advance', { initialValue: this.state.hotel.hotel_advance,
                                      valuePropName: 'checked',
                                    })(
                                      <Checkbox>
                                        Hotel Advance
                                      </Checkbox>,
                                    )}
                                  </Form.Item>
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item {...tailFormItemLayout}>
                                    {getFieldDecorator('hotel_roommafia', { initialValue: this.state.hotel.hotel_roommafia,
                                      valuePropName: 'checked',
                                    })(
                                      <Checkbox>
                                        Hotel Roommafia
                                      </Checkbox>,
                                    )}
                                  </Form.Item>
                                  </Col>
                                  <Col md={6}>
                                  <Form.Item label="Status" hasFeedback {...formItemLayout}>
                                    {getFieldDecorator('hotel_status', { initialValue: this.state.hotel.hotel_status,
                                      rules: [
                                        {
                                          required: true,
                                          type: 'boolean',
                                        },
                                      ],
                                    })(
                                      <Radio.Group name="radiogroup">
                                        <Radio value={true}>
                                          Active
                                        </Radio >
                                        <Radio value={false}>
                                          Disable
                                        </Radio >
                                      </Radio.Group>
                                    )}
                                  </Form.Item>
                                  </Col>
                                  
                                  <Form.Item {...tailFormItemLayout}>
                                    <Button type="primary" htmlType="submit">
                                      Submit
                                    </Button>
                                  </Form.Item>
                                  </Row>
                                </Form>
                                </Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
      );
    }
  }
  
  const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
   
  export default WrappedRegistrationForm;
